import { FC, ReactNode, useState } from 'react';
import enUS from 'antd/lib/locale/en_US';        // antd 
import zhCN from 'antd/lib/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import i18n from '../i18n';         //   i18n 
import { ConfigProvider } from 'antd';
import MyButton from '../components/MyButton';
import Index from '../pages';
// import { useLocation } from 'react-router'
import { useLocation } from "react-router-dom";
import { LangType } from "./index"

export interface LanguageProps {
    children: ReactNode
}
const Language: FC<LanguageProps> = (props) => {
    // http://localhost:3000/#zh-CN
    const { children } = props

    //    
    const [pre_lang, setPreLang] = useState(LangType.EN);     // 
    const [pre_name, setPreName] = useState(""); //  
    const [locale, setLocale] = useState(enUS); //  
    const [flag, setFlag] = useState(false)

    // ,  i18n useTranslation
    // let {i18n} = useTranslation()          // i18n
    //    {} , 

    // 
    const changeLang = (props: { lType: string }) => {
        const { lType } = props
        setFlag(true)
        switch (lType) {
            case LangType.EN:
                i18n.changeLanguage('en')     // i18n  
                moment.locale('en');          // antd 

                setPreLang(LangType.EN)
                setPreName("")
                setLocale(enUS)               // antd 
                break;
            case LangType.ZH:
                i18n.changeLanguage('zh')
                moment.locale('zh-cn');

                setPreLang(LangType.ZH)
                setPreName("")
                setLocale(zhCN)
                break;
        }
    }


    const location = useLocation()
    const routerName = () => {
        console.log(11111,location)
        let { hash } = location

        if (flag) { return }
        if (hash.indexOf(LangType.ZH) !== -1) {
            changeLang({ lType: LangType.ZH })
        }
        if (hash.indexOf(LangType.EN) !== -1) {
            changeLang({ lType: LangType.EN })
        }
    }
    routerName()

    return (
        <div className="TestChangeI18n">
            {/*,     */}
            {/* <MyButton changeLang={changeLang} name={pre_name} /> */}

            {/* antd  */}
            <ConfigProvider locale={locale}>
                {children}
            </ConfigProvider>
        </div>
    );
}

export default Language;
